<template>
    <div>
        <div
            style="display:flex; justify-content: space-between; background-color: #e8d8a7; padding: 10px; margin-bottom: 10px"
        >
            <div class="title-and-input" style="margin-bottom:5px;">
                <div style="display: flex; gap: 10px;">
                    <p-button
                        label="Back"
                        class="p-button-sm"
                        style="margin-bottom: 10px;"
                        @click="goToBarcodePage"
                    />
                    <label>{{ sessionId }}</label>
                </div>
                <label for="barcode-scanner">Please scan barcode here</label>
                <input
                    type="text"
                    class="p-inputtext"
                    v-model="scannedBarcode"
                    placeholder="Scan Barcode"
                    id="barcode-scanner"
                    @keyup.enter="handleBarcodeScan"
                    ref="inputRef"
                    v-on:blur="fopcusOut"
                    style="width:250px;"
                />
            </div>
            <div class="section">
                <div>
                    <div
                        style="display: inline-block;"
                        v-if="scan_person !== null"
                        :key="index"
                    >
                        <div
                            class="block-item1"
                            style="background-color:#FFFFFF;"
                        >
                            <div style="margin-right: 15px; ">
                                <img
                                    v-if="patientPicture"
                                    style="width: 100px; height: 100px;"
                                    :src="patientPicture"
                                />
                                <img
                                    v-else-if="scan_person.gender == 'F'"
                                    style="width: 100px; height: 100px;"
                                    src="../../assets/women.png"
                                />
                                <img
                                    v-else
                                    style="width: 100px; height: 100px;"
                                    src="../../assets/women.png"
                                />
                            </div>
                            <div
                                class="inner-block"
                                style="background-color:#d3D3D3;"
                            >
                                <div
                                    style="margin-bottom: 10px; display: flex; justify-content: space-between;"
                                >
                                    <div
                                        style="display: flex; flex-direction: column; gap: 2px;"
                                    >
                                        <span
                                            >{{ scan_person.patientId }}
                                            {{ scan_person.patientFirstName }}
                                            {{
                                                scan_person.patientLastName
                                            }}</span
                                        >
                                        <span
                                            >{{ scan_person.patientDOB }}
                                            {{ scan_person.gender }}</span
                                        >
                                    </div>
                                </div>
                                <div>
                                    <p-button
                                        label="Show History"
                                        :style="{
                                            width: 200 + 'px'
                                        }"
                                        @click="showHistoryDialog"
                                    />
                                </div>
                            </div>
                            <div
                                style="width: 100px; font-style: italic; display:flex; justify-items:center; align-items:center;"
                            >
                                <center>{{ scan_person.scannedAT }}</center>
                            </div>
                            <div
                                class="inner-block"
                                style="background-color:#d3D3D3;"
                                v-if="scan_person.partnerId !== null"
                            >
                                <div
                                    style="margin-bottom: 10px; display: flex; justify-content: space-between;"
                                >
                                    <div
                                        style="display: flex; flex-direction: column; gap: 2px;"
                                    >
                                        <span
                                            >{{ scan_person.partnerId }}
                                            {{ scan_person.partnerFirstName }}
                                            {{
                                                scan_person.partnerLastName
                                            }}</span
                                        >
                                        <span
                                            >{{ scan_person.partnerDOB }}
                                            {{
                                                scan_person.partnerGender
                                            }}</span
                                        >
                                    </div>
                                </div>
                                <div>
                                    <p-button
                                        label="Show History"
                                        :style="{
                                            width: 200 + 'px'
                                        }"
                                        @click="showHistoryDialog"
                                    />
                                </div>
                            </div>
                            <div style="margin-left: 15px; ">
                                <img
                                    v-if="partnerPicture"
                                    style="width: 100px; height: 100px;"
                                    :src="partnerPicture"
                                />
                                <img
                                    v-else-if="scan_person.partnerGender == 'F'"
                                    style="width: 100px; height: 100px;"
                                    src="../../assets/women.png"
                                />
                                <img
                                    v-else
                                    style="width: 100px; height: 100px;"
                                    src="../../assets/men.png"
                                />
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <div>
            <div class="section">
                <div>
                    <div
                        style="display: inline-block;"
                        v-for="(item, index) in scan_items"
                        :key="index"
                    >
                        <div
                            class="block-item"
                            :style="{
                                'background-color':
                                    item.type !== 'person'
                                        ? '#FC94AF'
                                        : '#FFFFFF'
                            }"
                        >
                            <div style="margin-right: 15px; ">
                                <p>{{ item.title }}</p>
                                <img
                                    v-if="item.type === 'person'"
                                    style="width: 100px; height: 100px;"
                                    src="../../assets/women.png"
                                />
                                <p
                                    v-else
                                    style="width: 100px; height: 100px; ;"
                                />
                            </div>
                            <div
                                class="inner-block"
                                :style="{
                                    'background-color':
                                        item.type !== 'person'
                                            ? '#D4A373'
                                            : '#d3D3D3'
                                }"
                            >
                                <div
                                    style="margin-bottom: 10px; display: flex; justify-content: space-between;"
                                >
                                    <div
                                        style="display: flex; flex-direction: column; gap: 2px;"
                                    >
                                        <span>{{ item.id }}</span>
                                        <span>{{ item.firstName }}</span>
                                        <span>{{ item.lastName }}</span>
                                        <span>{{ item.dob }}</span>
                                        <span>{{ item.gender }}</span>
                                        <span>{{ item.scannedAt }}</span>
                                    </div>
                                    <div>
                                        <p-button
                                            icon="pi pi-times"
                                            class="p-button-sm"
                                            @click="closeBlockItem(item, index)"
                                        />
                                    </div>
                                </div>
                                <div>
                                    <p-button
                                        label="Show History"
                                        :style="{
                                            width: 200 + 'px'
                                        }"
                                        @click="showHistoryDialog"
                                    />
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <div style="display: flex; justify-content: space-between;">
            <div style="display: flex; gap: 10px;">
                <div>
                    <p-button
                        label="Reset"
                        class="p-button-lg p-button-danger"
                        @click="resetScanItems"
                    />
                </div>
                <div v-if="personId">
                    <p-button
                        label="Report"
                        class="p-button-lg"
                        @click="showScanBarcodeDataPage"
                    />
                </div>
            </div>
            <div>
                <p-dropdown
                    v-model="selectedOption"
                    id="selectedOption"
                    :options="actionOptions"
                    optionLabel="label"
                    optionValue="value"
                    :showClear="true"
                    style="min-width: 400px; background-color: #e8d8a7; min-height: 38px;"
                />
                <p-button
                    label="Save"
                    class="p-button-lg"
                    style="margin-left: 10px;"
                    @click="saveScanItems"
                />
            </div>
        </div>
        <p-dialog
            header="Scanned History"
            v-model:visible="historyDialogVisible"
            @onHide="closeHistoryDialog"
            style="width: 1000px;"
        >
            <patient-scan-history :patientId="personId" />
        </p-dialog>
        <p-dialog
            v-model:visible="invalidBarcodeDialogVisible"
            :modal="true"
            :closable="false"
            style="width: 1400px;"
        >
            <div
                v-for="(msg, index) in warningMessages"
                :key="index"
                class="flex p-mt-6"
                style="display: flex; justify-content: center;"
            >
                <i
                    class="pi pi-exclamation-triangle"
                    style="font-size: 160px; color: red; margin-right: 20px;"
                ></i>
                <strong
                    class="p-text-bold"
                    :style="{ fontSize: '150px', color: 'red' }"
                    >{{ msg.title }}</strong
                >
            </div>
            <div>
                <p
                    style="font-size: 80px; color: red; display: flex; justify-content: center; font-weight: bold;"
                >
                    INVALID BARCODE
                </p>
            </div>
            <div class="p-field p-mt-6">
                <label
                    for="reason"
                    class="p-mr-2 font-medium"
                    style="width: 100px;"
                    >Username</label
                >
                <label for="reason" class="p-mr-2 font-medium">{{
                    store.state.user.username
                }}</label>
            </div>
            <div class="p-field">
                <label
                    for="reason"
                    class="p-mr-2 font-medium"
                    style="width: 100px;"
                    >Time</label
                >
                <label for="reason" class="p-mr-2 font-medium">{{
                    currentTime
                }}</label>
            </div>
            <div class="p-field">
                <label
                    for="reason"
                    class="p-mr-2 font-medium"
                    style="width: 100px;"
                    >Date</label
                >
                <label for="reason" class="p-mr-2 font-medium">{{
                    currentDate
                }}</label>
            </div>
            <div class="p-field">
                <label for="reason" class="block p-mr-2 font-medium"
                    >Reason<span class="text-red-500">*</span></label
                >
                <p-input-text
                    id="reason"
                    v-model="barcodeReason"
                    style="width: 750px;"
                    :class="{ 'p-invalid': showValidation && !barcodeReason }"
                />
                <small
                    v-if="showValidation && !barcodeReason"
                    class="p-error p-ml-2"
                    >Reason is required.</small
                >
            </div>
            <div class="p-mt-4">
                <p-button
                    label="Close"
                    icon="pi pi-times"
                    @click="handleCloseWarningDialog"
                />
            </div>
        </p-dialog>
    </div>
</template>
<script>
import { $axios } from "@/service/app-http";
import { onMounted, ref, watch, onBeforeUnmount } from "vue";
import { useStore } from "vuex";
import { serialize } from "php-serialize";
import { ZEBRA_PRING_URL, NL_FORM_BASE } from "@/constants";
import { ORIGIN } from "./../../config";
import { useToast } from "primevue/usetoast";
import { Dialog } from "primevue/dialog";
import PatientScanHistory from "@/components/test/patient-scan-history.vue";
import { useRouter } from "vue-router";

export default {
    components: {
        PatientScanHistory
    },
    setup() {
        const store = useStore();
        const inputRef = ref(null);
        const loading = ref(false);
        const patientPicture = ref(null);
        const partnerPicture = ref(null);

        const scannedBarcode = ref(null);
        let focusing = false; // A flag to track if focusing is in progress
        const toast = useToast();
        const personId = ref(null);
        const personId2 = ref(null);
        const sessionId = ref(null);
        const selectedOption = ref(null);
        const patient = ref({});
        const partner = ref({});
        const historyDialogVisible = ref(false);
        const historyData = ref([]);
        const scan_items = ref([]);
        const scan_person = ref(null);
        const actionOptions = ref([
            {
                label: "ICSI",
                value: "ICSI"
            },
            {
                label: "stripping",
                value: "stripping"
            },
            {
                label: "Ca++",
                value: "Ca++"
            },
            {
                label: "IVF",
                value: "IVF"
            },
            {
                label: "transfer to ET dish",
                value: "transfer to ET dish"
            },
            {
                label: "fertilization check",
                value: "fertilization check"
            },
            {
                label: "day 3",
                value: "day 3"
            },
            {
                label: "day 5",
                value: "day 5"
            },
            {
                label: "OPU",
                value: "OPU"
            },
            {
                label: "ET",
                value: "ET"
            },
            {
                label: "embryo glue",
                value: "embryo glue"
            },
            {
                label: "vitrify",
                value: "vitrify"
            },
            {
                label: "insemination",
                value: "insemination"
            }
        ]);

        const currentTime = ref("");
        const currentDate = ref("");

        const updateDateTime = () => {
            const now = new Date();
            currentTime.value = now.toLocaleTimeString();
            currentDate.value = now.toLocaleDateString();
        };

        const invalidBarcodeDialogVisible = ref(false);
        const warningMessages = ref([]);
        const barcodeReason = ref("");

        const showHistoryDialog = () => {
            historyDialogVisible.value = true;
        };

        const closeHistoryDialog = () => {
            historyDialogVisible.value = false;
        };
        // Function to add a scanned item to scan_items if it's not already present
        const addScannedItem = (item) => {
            const barcodeExists = scan_items.value.some(
                (existingItem) => existingItem.id === item.id
            );
            if (!barcodeExists) {
                scan_items.value.push(item);
            }
        };

        let savedBarcode = "";
        // Function to handle barcode scanning
        const handleBarcodeScan = () => {
            // Process barcode data and create the item object
            // Replace the following line with your barcode data processing logic
            // console.log("Barcode scanned:", scannedBarcode.value);
            savedBarcode = scannedBarcode.value;
            processBarcodeData(scannedBarcode.value);
            scannedBarcode.value = "";
        };

        const processBarcodeData = async (barcode) => {
            if (!barcode) return;
            try {
                if (!isNaN(barcode)) {
                    // If barcode is a number: get person data
                    if (scan_person.value === null) {
                        const { data } = await $axios.get(
                            `lab-barcode/get-person-data/${barcode}`
                        );

                        personId.value = data.patientId;
                        personId2.value = data.partnerId;
                        sessionId.value =
                            store.state.user.id + Date.now().toString(36);
                        scan_person.value = data;

                        getProfilePictures(data.patientId, data.partnerId);
                    }
                } else {
                    // Barcode is a container
                    let data;

                    if (personId.value !== null) {
                        let patientPartner = personId.value;

                        if (personId2.value !== null) {
                            patientPartner += "." + personId2.value;
                        }

                        const response = await $axios.get(
                            `lab-barcode/get-patient-barcode-data/${patientPartner}/${barcode}`
                        );
                        data = response.data;
                    } else {
                        const response = await $axios.get(
                            `lab-barcode/get-barcode-data/${barcode}`
                        );
                        data = response.data;

                        const person = data.labPatient;
                        personId.value = person.patientId;
                        personId2.value = person.partnerId;
                        sessionId.value =
                            store.state.user.id + Date.now().toString(36);
                        scan_person.value = person;

                        getProfilePictures(person.patientId, person.partnerId);
                    }

                    const itemData = data.labBarcode || data;

                    const scannedItem = {
                        id: itemData.barcode,
                        type: "container",
                        title: itemData.containerName,
                        logo: "logo1",
                        firstName: itemData.patientFirstName,
                        lastName: itemData.patientLastName,
                        dob: itemData.patientDOB,
                        gender: itemData.sex,
                        scannedAt: itemData.scannedAT
                    };

                    addScannedItem(scannedItem);
                }
            } catch (err) {
                warningMessages.value = [{ title: "WARNING !" }];
                invalidBarcodeDialogVisible.value = true;
            }
        };

        const showValidation = ref(false);

        const handleCloseWarningDialog = async (barcode) => {
            showValidation.value = true;

            if (!barcodeReason.value) {
                return;
            }

            const payload = {
                username: store.state.user.username,
                time: currentTime.value,
                date: currentDate.value,
                reason: barcodeReason.value,
                barcode: savedBarcode,
                personId: personId.value,
                personId2: personId2.value
            };

            try {
                await $axios.post("lab-test/post-invalid-barcode", payload);
                barcodeReason.value = "";
                showValidation.value = false;
                invalidBarcodeDialogVisible.value = false;
            } catch (error) {
                // console.error('Failed to submit barcode error:', error)
            }
        };

        const getProfilePictures = async (patientId, partnerId) => {
            if (patientId) {
                try {
                    const { data } = await $axios.get(
                        `oscar/patient/pp?patientId=${patientId}`
                    );

                    patientPicture.value = data.photoBase64;
                    //console.log(data);
                } catch (err) {}
            }

            if (partnerId) {
                try {
                    const { data } = await $axios.get(
                        `oscar/patient/pp?patientId=${partnerId}`
                    );

                    partnerPicture.value = data.photoBase64;
                } catch (err) {}
            }
        };

        const closeBlockItem = (item, index) => {
            // Use splice to remove the item from scan_items by index

            if (item.type === "person") scan_items.value = [];
            else scan_items.value.splice(index, 1);
        };

        const saveScanItems = async () => {
            if (selectedOption !== null || selectedOption !== "") {
                const payload = {
                    action: selectedOption.value,
                    patientId: personId.value,
                    partnerId: personId2.value,
                    sessionId: sessionId.value,
                    scannedItems: scan_items.value
                };

                try {
                    const { data } = await $axios.post(
                        `lab-barcode/save-scans`,
                        payload
                    );
                    // console.log(data);

                    toast.add({
                        severity: "success",
                        summary: "Success",
                        detail: "Scan items saved successfully",
                        life: 5000 // Adjust this value as needed
                    });
                } catch (err) {}
            }
        };

        const resetScanItems = () => {
            scan_items.value = [];
            scan_person.value = null;
            personId.value = null;
            personId2.value = null;
            sessionId.value = store.state.user.id + Date.now().toString(36);
        };

        const router = useRouter();
        onMounted(() => {
            updateDateTime();
            focusInput();
        });

        watch(scannedBarcode, () => {
            if (!focusing && !invalidBarcodeDialogVisible.value) {
                focusInput();
            }
        });
        const focusInput = () => {
            focusing = true;
            if (inputRef.value) {
                inputRef.value.focus();
            }
            focusing = false;
        };

        const fopcusOut = () => {
            if (!invalidBarcodeDialogVisible.value) {
                focusing = false;
                focusInput();
            }
        };

        return {
            loading,
            patient,
            partner,
            partnerPicture,
            patientPicture,
            personId,
            personId2,
            sessionId,
            store,
            scan_items,
            scannedBarcode,
            handleBarcodeScan,
            closeBlockItem,
            saveScanItems,
            selectedOption,
            toast,
            inputRef,
            fopcusOut,
            historyDialogVisible,
            historyData,
            showHistoryDialog,
            closeHistoryDialog,
            scan_person,
            actionOptions,
            resetScanItems,
            goToBarcodePage: () => {
                router.push({ path: "/barcodes" });
            },
            invalidBarcodeDialogVisible,
            warningMessages,
            barcodeReason,
            handleCloseWarningDialog,
            showValidation,
            currentTime,
            currentDate,
            showScanBarcodeDataPage: () => {
                const url =
                    NL_FORM_BASE +
                    "/cycles-reports/print-scan-barcode-data.php?patientId=" +
                    personId.value;
                window.open(url);
            }
        };
    }
};
</script>

<style scoped>
.p-card .p-card-content {
    padding: 0;
}

.block-item1 {
    display: flex;
    border: 1px solid #8cb3d9;
    padding: 10px;
}

.block-item {
    display: flex;
    border: 1px solid #8cb3d9;
    padding: 10px;
    margin-right: 10px;
    margin-bottom: 10px;
}

.title-and-input {
    display: flex;
    flex-direction: column;
    gap: 5px;
    margin-left: 10px;
    margin-top: 35px;
}

.inner-block {
    padding: 10px;
}

.separator {
    width: 1px;
    background-color: #000; /* Set the color of the separator */
    margin: 0 10px; /* Adjust the margin to control the spacing */
}
</style>
